@use 'base';
body {
  font-size: 16px;
}

.board {
  position: absolute;
  overflow-x: auto;
  overflow-y: hidden;
  user-select: none;
  white-space: nowrap;
  display: flex;
  margin: 0;
  top: 50px;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0;

  ul {
    list-style: none;
    padding: 0;
  }
}

.list-column {
  display: flex;
  align-items: flex-start;
  margin: 0;
}

.list-creator {
  width: min-content;
  height: min-content;
  margin: 1rem;
  font-size: 1.5em;
}

textarea {
  border: none;
  resize: none;
}

header {
  display: flex;
  height: 50px;
  justify-content: end;
}

input {
  width: 100%;
  box-sizing: border-box;
}

button {
  cursor: pointer;
}

header button{
  background: inherit;
  border: none;
  padding: 0;
  margin: .5em;
}

.light-svg {
  color: #999;
  font-size: 1.75em;
}

.light-svg:hover {
  color: base.$dark-color;
}

.dark-svg {
  color: #999;
  font-size: 1.75em;
}

.dark-svg:hover {
  color: base.$light-color;
}

.github {
  color: #999;
}

.github-svg {
  font-size: 1.5em;
}

.light-github:hover {
  color: base.$light-color;
}

.dark-github:hover {
  color: base.$dark-color;
}